import facultyUsers from "@/data/faculty.json";
import facultyDashboards from "@/data/dashboards/faculty.json";
import type { DashboardStats, FacultyMember } from "@/types/faculty";

interface FacultyUserRecord {
  id: string;
  username: string;
  dashboardId: string;
}

interface FacultyDashboardRecord {
  id: string;
  stats: DashboardStats;
  facultyMembers: FacultyMember[];
}

export interface FacultyDashboardData {
  stats: DashboardStats;
  facultyMembers: FacultyMember[];
}

export function getFacultyDashboardData(
  username?: string | null,
): FacultyDashboardData {
  const users = facultyUsers as FacultyUserRecord[];
  const dashboards = facultyDashboards as FacultyDashboardRecord[];

  const selectedUser = username
    ? users.find((user) => user.username === username)
    : users[0];

  const dashboard = selectedUser
    ? dashboards.find((item) => item.id === selectedUser.dashboardId)
    : dashboards[0];

  return {
    stats: dashboard?.stats ?? dashboards[0].stats,
    facultyMembers: dashboard?.facultyMembers ?? dashboards[0].facultyMembers,
  };
}
