"use client";

import { useMemo, useState } from "react";
import { FacultyPortfolio } from "./FacultyPortfolio";
import { DashboardHeader } from "./DashboardHeader";
import { StatsOverview } from "./StatsOverview";
import { PendingAlerts } from "./PendingAlerts";
import { ActivitySection } from "./ActivitySection";
import { AllFacultyMembers } from "./AllFacultyMembers";
import { FacultyMember } from "@/types/faculty";
import { useAuth } from "@/context/AuthContext";
import { getFacultyDashboardData } from "@/lib/dashboardData";

export function FacultyDashboard() {
  const [selectedFaculty, setSelectedFaculty] = useState<FacultyMember | null>(
    null,
  );
  const { user } = useAuth();
  const { stats, facultyMembers } = useMemo(
    () => getFacultyDashboardData(user?.username),
    [user?.username],
  );

  if (selectedFaculty) {
    return (
      <FacultyPortfolio
        faculty={selectedFaculty}
        onBack={() => setSelectedFaculty(null)}
      />
    );
  }

  return (
    <div className="space-y-6">
      <DashboardHeader />
      <StatsOverview stats={stats} />
      <PendingAlerts pendingReports={stats.pendingReports} />
      <ActivitySection
        activities={stats.recentActivity}
        facultyMembers={facultyMembers}
        onSelectFaculty={setSelectedFaculty}
      />
      <AllFacultyMembers
        facultyMembers={facultyMembers}
        onSelectFaculty={setSelectedFaculty}
      />
    </div>
  );
}
